@font-face {
  font-family: averta;
  src: url(./fonts/AvertaDemoPECuttedDemo-Regular.otf) format("otf"),
  url(./fonts/AvertaDemoPE-ExtraBold.otf) format(otf);
  font-display: optional;
  font-weight: 600;
}
.rsponsive-nav{
  display: none;
}
@font-face {
  font-family: __averta_Fallback_cd5f11;
  src: local("Arial");
  ascent-override: 99.76%;
  descent-override: 26.48%;
  line-gap-override: 0%;
  size-adjust: 104.35%;
}
body{
  font-family: averta, __averta_Fallback_cd5f11;
}
.logo{
  h1{
    font-family: cursive;
    font-size: 1.5rem;
    margin-bottom: 0;
    -webkit-text-stroke: 1.1px #000;
    color: transparent;
    font-weight: 800;
    letter-spacing: 0.05em;
    text-shadow: none;
    text-transform: uppercase;
  }
}

nav {
  ul {
    li {
      padding-right: 3rem;
      list-style: none;
      a {
        text-decoration: none;
        color: black;
        font-family: averta, __averta_Fallback_cd5f11;
        font-weight: 600;
        line-height: 1;
        letter-spacing: 1px;
      }
    }
  }
}
.nav-btn {
  .btn {
    padding: 1rem 3.1rem;
    line-height: 1.5;
    text-align: center;
    max-width: 24.2rem;
    border-radius: 2px;
    color: #fff;
    background-color: #111;
    border: 1px solid #111;
    font-family: averta, __averta_Fallback_cd5f11;
    font-weight: 600;
    letter-spacing: 1px;
  }
}
.hero{
  padding-top: 12rem;

.hero-section{
  h1{
       max-width: 890px;
    margin: auto;
    -webkit-text-stroke: 0.1px #000;
    color: transparent;
    font-size: 58px;
    font-weight: 900;
    letter-spacing: .05em;
    line-height: 70px;
    text-shadow: none;
    text-transform: uppercase;
  }
  p{
    font-family: averta, __averta_Fallback_cd5f11;
    text-align: center;
    color: #707070;
    letter-spacing: 0;
    font-weight: 600;


    span{
      color: #ff3c2e;
    }
  }


}
.heroSiblingSection{
  margin-top: 9rem;
  .col-sm-4{
    text-align: center;
    h2{
      font-family: averta, __averta_Fallback_cd5f11;
      font-weight: 600;
      
    }
    p{
      max-width: 12.5rem;
      color: #707070;
      font-weight: 600;
      margin: auto;
      letter-spacing: .53px;
      font-family: averta, __averta_Fallback_cd5f11;
    }
  }
  
  }
  .hero-image{
margin-top: 9rem;
margin-bottom: 4rem;
    .col-12{
      
      .image{
       
        img{
          width: 100%;
        }
      }
    }
  }
}
.nav-item{

  a{
    font-weight: 600;
  }
}

.caseimg{
  img {
width: 100%;
  }
}
.firstSection{
  h1{
    font-weight: 600;
  }
  h2{
    
    font-family: lato;
    font-style: normal;
       max-width: 890px;
    margin: auto;
    -webkit-text-stroke: 1.235px #000;
    color: transparent;
    font-size: 58px;
    font-weight: 900;
    letter-spacing: .05em;
    line-height: 70px;
    text-shadow: none;
    text-transform: uppercase;

  }
  p{
    font-family: lato;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: .277778px;
    color: #8e8ea1;
  }
  button{
    display: flex;
    justify-content: center;
    align-items: center;
    /* flex: 0 1 236px; */
    padding: 3px 83px;
    border-radius: 24px;
    font-family: lato, roboto, sans-serif;
    font-size: inherit;
    line-height: 3em;
    border: 1px solid black;
    font-weight: 700;
    letter-spacing: 0.5px;
    color: #fff;
margin-top: 1.9rem;
    background-color: #27273d;
    transition: 0.3s ease-in;
    box-shadow: 0 15px 30px rgba(64,64,137,.2);
  }
  button:hover{

    background-color:white ;
  border: 1px solid black;
  color: black;
 
  }
}
// portfolio-section

.portfolio-section{
  
  .row {

    .col-lg-6{

      .pImg-2{
      width: 85%;  
      }
    }
  }
}
@media screen and (max-width:1412px){
  .nav-btn{
   display: none;
  }
}
@media screen and (max-width:996px){
  .rsponsive-nav{
   display: block;
  }
  .nav-first{
    display: none;
  }
  .pImg-1{
    width: 90%!important;
    }
    .pImg-2{
      margin-top: 2rem!important;
      width: 90%!important;
      margin: auto;  
      }
}
@media screen and (max-width:578px){
  .hero {
    padding-top: 6rem;
  }
  .hero .hero-section h1 {
    font-size: 40px;
    line-height: 63px;
}
.hero .heroSiblingSection .col-sm-4 {
  text-align: center;
  margin-bottom: 4rem;
}
.hero .heroSiblingSection {
  margin-top: 4rem;
}
.hero .hero-image {
  margin-top: 3rem;
  margin-bottom: 4rem;
}
.firstSection h2{
  font-size: 45px;
}

}

@media screen and (max-width:300px){

.firstSection button{
  padding: 3px 56px;
}
.firstSection h2{
  font-size: 30px;
  line-height: 46px;
}
}
